/* -----------------------------------------------------------------------------
  Root (inc)
----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

.inner-body {
  position: relative;
  width: 100%;
}

h1,
.rubric.h1 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 34px;
  font-weight: 800;
  letter-spacing: .03em;
}

h2,
.rubric.h2 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 28px;
  font-weight: 800;
  letter-spacing: .03em;
}

h3,
.rubric.h3 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 22px;
  font-weight: 800;
  letter-spacing: .03em;
}

h4,
.rubric.h4 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 18px;
  font-weight: 800;
  letter-spacing: .03em;
}

h5,
.rubric.h5 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 800;
  letter-spacing: .03em;
}

h6,
.rubric.h6 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 800;
  letter-spacing: .03em;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a:active {
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* Light theme -------------------------------------------------------------- */
body {
  background-color: #fff;
  color: #000;
}

body a {
  color: #256bd1;
}

body a:hover, body a:focus {
  color: #3585f9;
}

body a:active {
  color: #256bd1;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme {
  background-color: #2a2d34;
  color: #fff;
}

.dark-theme a {
  color: #499cfe;
}

.dark-theme a:hover, .dark-theme a:focus {
  color: #6ca2f0;
}

.dark-theme a:active {
  color: #499cfe;
}

