/* -----------------------------------------------------------------------------
  Component: Breadcrumbs

  Style for the breadcrumbs present in the content area.
----------------------------------------------------------------------------- */
#breadcrumbs {
  position: fixed;
  top: 77px;
  display: inline-flex;
  flex-direction: row;
  align-items: flex-end;
  width: calc(100% - 385px - 290px);
  height: 60px;
  padding: 5px 15px 5px 30px;
  margin: 0;
  list-style: none;
  z-index: 99;
}

#breadcrumbs .breadcrumb-separator::after {
  display: inline-block;
  margin: 0 15px;
  content: "/";
  font-size: 1.1rem;
}

/* Light theme -------------------------------------------------------------- */
body #breadcrumbs {
  color: #000;
  background-color: #fff;
}

body #breadcrumbs .breadcrumb-separator::after {
  color: #000;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme #breadcrumbs {
  color: #fff;
  background-color: #2a2d34;
}

.dark-theme #breadcrumbs .breadcrumb-separator::after {
  color: #fff;
}

/* -----------------------------------------------------------------------------
  Component: Code-blocks

  Style for the code-blocks and adjustment of the pygments.
----------------------------------------------------------------------------- */
main code,
main kbd,
main pre,
main samp {
  font-family: "Fira Mono", monospace;
}

.literal-block-wrapper.docutils.container {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
}

.literal-block-wrapper.docutils.container .code-block-caption {
  display: flex;
  height: 48px;
  align-items: center;
  border-radius: 10px 10px 0 0;
  padding: 5px 10px;
  justify-content: space-between;
  margin-top: 1rem;
}

.literal-block-wrapper.docutils.container .code-block-caption .headerlink {
  text-decoration: none;
  line-height: 1;
}

.literal-block-wrapper.docutils.container .code-block-caption .headerlink::after {
  vertical-align: middle;
  content: '\f0c1';
  display: inline-block;
  width: 1rem;
  height: 1rem;
  font: normal bold normal 0.95rem/1 FontAwesome;
  text-align: right;
  opacity: 0;
  transition: opacity ease-in .1s;
}

.literal-block-wrapper.docutils.container .code-block-caption:hover .headerlink::after {
  opacity: 1;
  transition: opacity ease-in .1s;
}

.literal-block-wrapper.docutils.container tbody tr td {
  padding: 0;
  border: none;
}

[class*="highlight-"] {
  max-width: 100%;
  max-height: 100vh;
  margin-bottom: 1rem;
  position: relative;
  transition: max-height .2s;
  overflow-y: hidden;
}

[class*="highlight-"].output .output-title {
  height: 40px;
  display: flex;
  align-items: center;
  border-radius: 10px 10px 0 0;
  font-weight: 700;
  width: 100%;
  cursor: pointer;
}

[class*="highlight-"].output .output-title::before {
  content: '';
  height: 40px;
  width: 40px;
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: center;
  transition: transform .1s;
}

[class*="highlight-"].output .linenodiv {
  top: 60px;
}

[class*="highlight-"].output.collapsed {
  max-height: 40px;
  transition: max-height .2s;
}

[class*="highlight-"].output.collapsed .output-title {
  border-radius: 10px;
  transition: border-radius 0.05s .2s;
}

[class*="highlight-"].output.collapsed .output-title::before {
  transform: rotateZ(-90deg);
  transition: transform .1s;
}

[class*="highlight-"] .highlighttable {
  table-layout: fixed;
  width: 100%;
  margin-bottom: 0;
}

[class*="highlight-"] .highlighttable .highlight {
  position: unset;
  overflow-x: auto;
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 20px;
}

[class*="highlight-"]:hover .copy-to-clipboard {
  opacity: 1;
}

[class*="highlight-"] .copy-to-clipboard {
  position: absolute;
  z-index: 10;
  top: 7px;
  right: 7px;
  cursor: pointer;
  opacity: 0;
  padding: 8px;
  font: normal normal normal 14px/1 FontAwesome;
  border: 0;
  border-radius: 10px;
  transition: all 0.2s ease;
}

[class*="highlight-"] .copy-to-clipboard:focus {
  opacity: 1;
}

[class*="highlight-"] .copy-to-clipboard.copied {
  opacity: 1;
}

[class*="highlight-"] .copy-to-clipboard span {
  display: none;
  font-size: 0.9rem;
  font-family: Manrope, sans-serif;
}

[class*="highlight-"] .no-select {
  user-select: none;
}

[class*="highlight-"] td.code pre {
  padding: 0 20px;
  overflow: visible;
  position: relative;
  z-index: 6;
}

[class*="highlight-"] .linenodiv {
  display: inline-flex;
  border-right: 1px solid;
  position: absolute;
  top: 20px;
  z-index: 10;
  width: 50px;
  justify-content: center;
  user-select: none;
}

[class*="highlight-"] .linenos {
  width: 48px;
}

[class*="highlight-"] .highlight {
  font-family: "Fira Mono", monospace;
  position: relative;
  padding: 20px 0 0;
  display: flex;
  overflow-x: auto;
  z-index: 0;
  line-height: 24px;
}

[class*="highlight-"] .highlight pre {
  margin: 0;
  position: unset;
  padding: 0 20px 20px;
}

[class*="highlight-"] .highlight .hll {
  display: block;
}

[class*="highlight-"] .highlight .hll::before {
  position: absolute;
  left: 0;
  right: 0;
  height: 24px;
  display: block;
  content: '';
  z-index: -20;
}

[class*="highlight-"] .highlight .hll > span {
  position: relative;
  z-index: 25;
}

[class*="highlight-"] .highlight .k,
[class*="highlight-"] .highlight .kn {
  font-weight: 700;
}

